@import '../../../shared/styles/variables.scss';

.navbar {
  &__list {
    display: flex;
    gap: 30px;
    list-style: none;
  }
}

.navlink {
  text-decoration: none;
  font-family: 'Lato';
  color: $white;
  text-transform: uppercase;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    width: 0;
    left: 0;
    right: 0;
    bottom: -3px;
    height: 1px;
    background-color: $white;
    transition: width 0.3s ease;
  }

  &:hover::before,
  &--active::before {
    width: 100%;
  }
}
